function Code1() {

    const codeText =
        `
// Input.tsx - form 내 코드
// Date는 총 6가지 타입은 아래와 같음

// 1.sentence : string
// 2.footNote? : string -> 5번 항목이 true 경우만 데이터가 있음.
// 3.nickName : string
// 4.passWord : string
// 5.footNoteCheckBox : boolean
// 6.changeParagraph : boolean

// 사용자 입력을 받는 코드는 생략
    `


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}


function Code2() {

    const codeText =
        `
// Input.tsx - sentenceSubmit 함수
// 이용자가 입력한 6개의 데이터는 useState에 저장됨.

const sentenceSubmit = 
async (e: React.FormEvent<HTMLFormElement>): Promise<void> =>
  {
    e.preventDefault();
    setInputAble(false) 
    // 작업처리를 위한 로딩 중 이용자의 입력을 막는 스위치 데이터
    const userInputData: SentenceUserInputDataTotal = {
        content: inputSentence,
        footnote: inputFootNote,
        nickname: inputNickname,
        password: inputPassword,
        footNoteCheckBox: footNoteCheckBox,
        changeParagraph: changeParagraph,
    } 
    // 각각의 이용자 입력데이터를 Obj 정리 
    // 이용자 입력데이터 타입 또한 Obj 형식으로 되어 있음.

    const validationResult: ValidationInputSentenceFucRetrunType
        = await validationInputSentence(userInputData)
    // function 폴더 validation.ts 내 
    // validationInputSentence 함수를 통한 검증 절차 시작.
    // 다음 코드 블럭 참고.

    if (validationResult.error) {
        alert(validationResult.error)
        // 검증 이상 있을 시 보여주는 에러 메세지.
    } else {
        // 검증 이상 없을 시 진행 되는 코드
        const thisBookMode = sentenceCounterSlice.mode; 
        // 모드 변수 할당 (끝말잇기 모드와 자유 모드)
        // 모드 구분은 boolean으로 되어 있음.  t = 자유 / f = 끝말잇기
        const afterValidationDate: SentenceUserInputType =
         validationResult.value
        // 검증이 끝난 데이터 그대로 변수 할당

        if (!thisBookMode) {
        // 끝말잇기 모드 일시.
            const { lastWords } = sentenceCounterSlice;
            // 앞 문장 마지막 글자들 변수 할당 
            // sentenceCounterSlice 내 string[] 타입으로 저장되어 있습니다.
            const inputFirstWord: string = inputSentence[0]
            // 현재 이용자가 입력한 첫번째 글자 가져오기
            if (!lastWords.includes(inputFirstWord) 
                && !sentenceCounterSlice.newStart) {
                setInputAble(true)
                return alert(\`반드시 \${lastWords} 중 한 글자로 시작하셔야합니다.\`)
                // 끝말 잇기 형식에서 벗어 났을 떄.
            }
        }

        // 끝말잇기 모드인 경우, 끝말잇기 문제 없다면 아래 코드 실행;
        // 자유 모드인 경우, 검증 이상 없다면 아래 코드 실행;

        const sentenceData: addSentencePostSocketInputType = {
            ...afterValidationDate,
            no: sentenceCounterSlice.sentenceCount + 1,
            expression: [0, 0, 0, 0],
            comments: 0,
            writeDate: currentDate,
            paragraph: sentenceCounterSlice.paragraphCount,
            changeParagraph: changeParagraph,
            params: id
        }

        // 이용자 입력 데이터에 필요한 데이터 추가
        // (입력시간, 문장 넘버, 감정 표헌 수 등등)

        addSentenceSocket(dispatch, sentenceData)
        
        // function 폴더 socketAction.ts 내 
        // addSentenceSocket 을 통해 데이터 전송 실행
        // Dispatch를 같이 보내는 이유
        // => 소설 관련 모든 데이터는 서버에서 데이터를
        // Redux toolkit 을 통해 관리하기 때문에
        // 접근 할 수 있는 dispatch를 매개변수로 넣음

        setInputSentece("")
        setInputFootNote("")
        setFootNoteCheckBox(false)
        setChangeParagraph(false)
        // 이용자 입력 데이터 초기화
        // 닉네임하고 비밀번호는 초기화 x
        setInputAble(true)
        // 작업처리 로딩 완료
    }

}
    `


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}

function Code3() {

    const codeText =
        `
// function 폴더 validation.ts 내  - validationInputSentence 함수
// 이 파일에선 유저가 작성한 문장 검증을 포함한 총 4개의 검증 함수가 있습니다.
// 그 중 문장에 대한 부분만 기재하였습니다.

const customValidation: CustomValidationType = (value, helpers) => {
    const regex = /[가-힣]+/g  // 완성형 글자 정규식
    const afterMatch = value.match(regex);
    // value에서 정규식에 맞는 한글 완성형 글자들을 배열로 추출
    const afterJoin = afterMatch ? afterMatch.join('') : '';
    // 추출된 한글 완성형 글자들을 문자열로 합침
    if (afterJoin.length < 4) {
        return helpers.error('string.custom', { massage: '완성형 한글이 최소 5글자 이상 있어야 합니다.' })
    }
    return value;
}
// 위 함수는 완성형 글자가 5글자 이상이 있는지
// 확인 하는 custom 검증 절차입니다.

const inputCheckKoreanPattern = /^[^\0-\x1F\x7F-\x9F]*$/;
// 문자열 정규식 (글씨 깨짐 현상으로 상단 확인)


const inputSentenceValidation = Joi.object({
    content: Joi.string().pattern(inputCheckKoreanPattern)
    .min(5).custom(customValidation, '한글 5글자 이상 여부').required()
        .messages({
            'string.empty': '입력 값이 비어있습니다. 입력바랍니다.',
            'string.min': '최소 5글자 이상 입력바랍니다.',
            'string.custom': '완성형 한글이 최소 5글자 이상 있어야 합니다.'
        })
        // 사용자 입력데이터 중 sentence(content) 검증 절차
    ,
    footnote: Joi.string().pattern(inputCheckKoreanPattern).min(5)
    .optional() 
        .messages({
            'string.empty': '입력 값이 비어있습니다. 입력바랍니다.',
            'string.min': '최소 5글자 이상 입력바랍니다.'
        })
        // 사용자 입력 데이터 중 footnote 검증 절차
        // 이 데이터는 존재하지 않을 수도 있음으로 optional() 추가
    ,
    nickname: Joi.string().min(2).max(8).required()
        .messages({
            'string.empty': '닉네임은 2글자 ~ 8글자 사이로 입력바랍니다.',
            'string.min': '닉네임은 2글자 ~ 8글자 사이로 입력바랍니다.',
            'string.max': '닉네임은 2글자 ~ 8글자 사이로 입력바랍니다.'
        })
        // 사용자 입력 데이터 중 nickname 검증 절차
    ,
    password: Joi.string().min(4).max(8).required()
        .messages({
            'string.empty': '비밀번호는 2글자 ~ 8글자 사이로 입력바랍니다.',
            'string.min': '비밀번호는 4글자 ~ 8글자 사이로 입력바랍니다.',
            'string.max': '비밀번호는 4글자 ~ 8글자 사이로 입력바랍니다.'
        })
        // 사용자 입력 데이터 중 password 검증 절차
})


export async function validationInputSentence(validationData: SentenceUserInputDataTotal)
:Promise<ValidationInputSentenceFucRetrunType> {

    // validationFucNum - 필요 Joi 검증 번호 / validationData - Joi 검증 대상 데이터
    const { footNoteCheckBox, changeParagraph, ...needValidate } = alidationData
    // footNoteCheckBox와 changeParagraph는 따로 검증이 필요하지 않아 제외.
    if (!footNoteCheckBox) await delete needValidate.footnote
    // footNoteCheckBox 여부에 따라 footnote 도 제외
    const { error, value } = await inputSentenceValidation.validate(needValidate)
    // 검증 절차 실행
    return { error, value }
    // 결과 값 리턴
    // 리턴 데이터 type
    // error : string | undefined
    // value : SentenceUserInputDataTotal (매개변수로 들어왔던 데이터 타입 그대로)
}

    `


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}

export default Function1;