function Code1() {

    const codeText =
        `
// BaseBall_Diry Front
// Login.js 내


// 설명에 필요한 코드만 가져왔습니다.
// 총 3개 항목에 대해서 기능을 구현하였습니다.
// 대표로 아이디 대한 검증를 적어보겠습니다.


//1. 이용자 데이터 입력
// form input 을 통하여 text 형태로 입력을 받아 state에 저장을 하였습니다.

<input type='text'
placeholder='- 5 ~ 15자 (영문/숫자)'
className='input'
name='username'
onChange={(e) => { formDataChange(e) }}
style={{ background: warningCss[0] }}
disabled={overlapCheck[0]}
autoComplete='off'
/>


//2. useEffect를 통하여 데이터 변경을 추적
// onChange로 state에 데이터가 저장(변경)되면 useEffect를 통하여 검증절차 진행
// 이용자가 마지막 입력 후 0.3초 이후에 검증 절차가 진행되게 하였으며
// 연속으로 입력 시, 코드 실행은 clearTimeout 으로 막고자 노력하였습니다.

useEffect(() => {
    let inputTime;
    if (inputTime) {
        clearTimeout(inputTime)
    }
    inputTime = setTimeout(() => {
        formDataExamine(0) 
        // 검증 절차 함수 실행
    }, 300);
    return () => {
        clearTimeout(inputTime)
    }
}, [myUserName])

// 3. 검증 절차 함수를 통한 검증 절차 실행
// 결과에 따라 state 저장이 되며 state 값에 style의 변화를 주어
// 이용자에게 안내를 하도록 노력하였습니다.

`


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}




export default Function9;

