function Code1() {

    const codeText =
        `
// BaseBall_Team Front
// Calender.js 내

const [value, onChange] = useState<Value>(new Date());
// React-Calender에서 선택한 날짜 데이터
const [schedule, setSchedule] = useState<Schedule[]>()
// 이용자의 일정이 담긴 데이터
const [scheduleSelect, setScheduleSelect] = useState<Schedule[]>([])
// 이용자가 선택한 날짜에 해당 되는 일정

<div className='CalenderMypage-wrap'>
    <Calendar
        onChange={onChange} // 날짜 선택 시 setState 로 value로 날짜 저장
        value={value} // 현재 선택한 날짜 (default 값은 Today)
        formatDay={(locale, date) => moment(date).format("D")} // 달력에 날짜 표기 방법 
        minDetail="year" // 달력 최소 검색범위
        maxDetail="month" // 달력 최대 검색 범위
        tileContent={({ date, view }) => {
            // 일자 별 Content 삽입 
            var dailySchedule = schedule?.filter(x => moment(x.date).format() === moment(date).format())
            // 해당 일자에 이용자 일정이 있는지 확인.
            if (dailySchedule?.length === 0) return;
            // 없다면 아무런 Content 삽입을 안함.
            return <div className='calendar-dot-wrap'>
            // 있다면 JSX 리턴.
                {dailySchedule?.map((val, idx) => {
                    return <div
                        key={idx}
                        className='calendar-dot'
                        style={{ background: val.calendartype === 1 ? "blue" : val.calendartype === 2 ? "green" : "red" }} />
                        // 일정이 여러개인 경우 데이터 타입에 따라 다른 색으로 표기
                })}
            </div>
        }}
        onClickDay={async (value) => {
            // React-Calender 에서 일자 클릭 시 
            if (schedule === undefined) return
            // 이용자 일정이 아무것도 없을 경우 리턴
            const clickDay = schedule?.filter((x) => moment(x.date).isSame(value))
            // 선택한 일자에 이용자 일정이 있을 시
            setScheduleSelect(clickDay)
            // 해당 일정을 새로운 state에 저장
        }}
    />
    <div className='calender-schedule'>
        // ~ 선택한 날짜에 대한 데이터 정보 표기 코드
    </div>
</div>


`


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}




export default Function10;
