import { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import './../../css/common/navPage.css'


function NavPage() {

    const navigate = useNavigate();
    const [bgPosition, setBgPosition] = useState({
        top: 0,
        right: 0,
        bottom: 0,
        left: 0
    })


    const navTextMouseEnter = (e) => {
        const navWrap = e.target.closest('.nav-wrap')
        if (!navWrap) return;
        const rect1 = navWrap.getBoundingClientRect();
        const rect2 = e.target.getBoundingClientRect();
        setBgPosition(pre => ({
            ...pre,
            left: rect2.left - rect1.left,
            right: rect1.right - rect2.right
        })
        )
    }

    const navTextMouseLeave = () => {
        setBgPosition(pre => ({
            ...pre,
            top: 0,
            right: 0,
            bottom: 0,
            left: 0
        }))
    }


    return (
        <div className='NavPage'>
            <div className='nav-wrap'>
                {/* <div className='nav-manu'>
                    <span
                        onMouseEnter={navTextMouseEnter}
                        onMouseLeave={navTextMouseLeave}
                        onClick={() => navigate('/')}
                    >
                        <p className="material-symbols-outlined nav-text">
                            home
                        </p>
                    </span>
                </div> */}
                <div className='nav-manu'>
                    <span
                        className='nav-text'
                        onMouseEnter={navTextMouseEnter}
                        onMouseLeave={navTextMouseLeave}
                        onClick={() => navigate('/')}
                    >
                        <p className='suse'>
                            Project
                        </p>
                    </span>
                </div>
                <div className='nav-manu'>
                    <span
                        className='nav-text'
                        onMouseEnter={navTextMouseEnter}
                        onMouseLeave={navTextMouseLeave}
                        onClick={() => navigate('/architecture')}
                    >
                        <p className='suse'>
                            Architecture
                        </p>
                    </span>
                </div>
                <div className='nav-manu'>
                    <span
                        className='nav-text'
                        onMouseEnter={navTextMouseEnter}
                        onMouseLeave={navTextMouseLeave}
                        onClick={() => navigate('/function')}
                    >
                        <p className='suse'>
                            Function
                        </p>
                    </span>
                </div>
                <div className='nav-manu'>
                    <span
                        className='nav-text'
                        onMouseEnter={navTextMouseEnter}
                        onMouseLeave={navTextMouseLeave}
                        onClick={() => navigate('/about')}
                    >
                        <p className='suse'>
                            About
                        </p>
                    </span>
                </div>
                <div
                    className='nav-background'
                    style={{
                        top: `${bgPosition.top}px`,
                        right: `${bgPosition.right}px`,
                        bottom: `${bgPosition.bottom}px`,
                        left: `${bgPosition.left}px`,
                    }}
                />
            </div>

        </div>
    )
}

export default NavPage