
import { useParams } from 'react-router-dom'
import './../../css/project/projectEX.css'

import Tagword from './tagword/TagWord';
import BaseBallTeam from './baseball/BaseBallTeam';
import BaseBallDiary from './baseball/BaseBallDiary';
import Pokemon from './pokemon/Pokemon';
import Dog from './dog/dog';
import Emotion from './emotion/Emotion';
import Mummumi from './dog/Mummumi';


//  EX => explanation (설명)
function ProjectEX() {
    let { id } = useParams();
    const projectNames = {
        mummumi: <Mummumi />,
        emotion: <Emotion />,
        dog: <Dog />,
        pokemon: <Pokemon />,
        baseballdiary: <BaseBallDiary />,
        baseballteam: <BaseBallTeam />,
        tagword: <Tagword />,
        makinghabit: 8
    }

    const checkParams = () => {
        const projectComponent = projectNames[id]
        return projectComponent
    }

    return (
        <div className='ProjectEX'>
            {checkParams()}
        </div>
    )
}

//  projectEx MW - 1024


export default ProjectEX
