function Code1() {

    const codeText =
        `
// BaseBall_Team Server
// app.js 

app.use(session({
  secret: **,
  resave: false, // 세션 저장 옵션
  saveUninitialized: false, // 세션 저장 옵션
  cookie: {
    maxAge: 60 * 60 * 1000, // 세션 쿠키 만료시간
    secure: true // 세션 쿠키 전송 옵션
  },
  store: MongoStore.create({  // 세션 데이터 저장 공간 지정
    // DB_URL 및 DB_NANE 은 생략 **
  })
}))

// 세션 발급에 대한 옵션 값입니다. 
// 먼저 서버 파일인 app.js 에 Passport 설치 후 
// 세션에 대한 옵션을 지정해줬습니다.

// 그 외 회원 기능에 대한 모든 코드는 routes를 사용하여
// login.js 파일에서 진행합니다.
`


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}

function Code2() {

    const codeText =
        `
// BaseBall_Team Server
// routes 폴더 내 login.js

// 1. 회원가입_
// 회원가입시 클라이언트는 아래와 같이 3개의 데이터를 서버로 보냅니다.
// username : string | nickname : string | password : string

router.post('/regist', async (req, res) => {
  try {
    const { username, password, nickname } = req.body 
    // 각각 데이터 변수 할당
    const hash_password = await bcrypt.hash(password, 10)
    // 비밀번호 Hash 처리
    const overlapping_result = await check_overlapping(username, nickname)
    // username 과 nickname 의 중복 확인
    // 리턴 데이터는 [boolean, [boolean,boolean]] 타입이며
    // idx0 은 결과값 / idx1은 각 데이터별 중복결과가 담겨있습니다.
    if (!overlapping_result[0]) { 
    // 중복 데이터가 없는 경우.
      await db.collection('user').insertOne({
        username: username,
        nickname: nickname,
        password: hash_password,
        regist_moment: moment().format('YYYY-MM-DD HH:mm'),
        team: { team1: null, team2: null }
      })
    // 'user' collection 에 이용자가 입력한 데이터와
    // 필요한 데이터를 추가하여 저장
      res.status(200).send({ success: true, message: "회원가입이 완료되었습니다." })
      return
    // 클라이언트로 완료 내용 전달
    } else if (overlapping_result[0]) {
    // 중복 데이터가 있는 경우.
      res.status(200).send({ success: false, overlapping_result: overlapping_result[1], false_code: 2, message: '중복데이터 -> false_code : 2' })
      return;
    // 클라이언트로 중복정보를 전달
    }
  } catch (err) {
    console.error("회원가입 insertOne 실패   -> " + err)
    res.status(500).send({ success: false, meassage: "DB등록에 실패하였습니다." })
    return
  }
})
    // username 이나 nickname 작성 시 중복확인 절차가 있으나
    // 회원가입 완료 과정에서 중복 체크를 다시 한번 진행

`


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}

function Code3() {

    const codeText =
        `
// BaseBall_Team Server
// routes 폴더 내 login.js

// 2. 로그인_
// 로그인시 클라이언트는 아래와 같이 2개의 데이터를 서버로 보냅니다.
// username : string | password : string
// 코드 처리 순서별로 코드를 나열했습니다.

// 2-1. 로그인 데이터 POST 처리
router.post('/login', async (req, res, next) => {
  passport.authenticate('local', (error, user, info) => {
    if (error) return res.status(500).json({ success: false, message: error })
    if (!user) return res.status(200).json({ success: false, message: info.message, false_code: 3 })
    req.logIn(user, (err) => {
      if (err) return next(err)
      res.status(200).send({ success: true })
    })
  })(req, res, next)
})
// 회원이 보낸 username 과 password를 passport.authenticate으로 보내서
// DB 내 데이터와 확인하게 됩니다.
// 검증 결과를 리턴값으로 받아 필요한 작업을 수행하게 됩니다.
// 문제가 없을 시 req.logIn을 통하여 2-3. 세션 발급 수행

// 2-2. DB 데이터와 비교확인
passport.use(new LocalStrategy(async (입력한아이디, 입력한비번, cb) => {
  let result = await db.collection('user').findOne({ username: 입력한아이디 })
  if (!result) {
    return cb(null, false, { message: '(id)입력데이터틀림 -> false_code : 3' })
  }
  if (await bcrypt.compare(입력한비번, result.password)) {
    return cb(null, result)
  } else {
    return cb(null, false, { message: '(pw)입력데이터틀림 -> false_code : 3' });
  }
}))
// DB 데이터를 가져 온 후, 아이디부터 순차적으로 비교확인 절차 실행
// 콜백함수 cd를 통해서 검증 결과를 리턴


//2-3. 로그인 성공시
//클라이언트로 세션쿠키 발급
//DB에 세션 데이터 저장
passport.serializeUser((user, done) => {
  process.nextTick(() => {
    done(null, { id: user._id, username: user.username })
  })
})

//2-4. 클라이언트에서 보낸 세션 데이터 확인
passport.deserializeUser(async (user, done) => {
  let result = await db.collection('user').findOne({ _id: new ObjectId(user.id) })
  delete result.password
  process.nextTick(() => {
    return done(null, result)
  })
})
`


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}

function Code4() {

    const codeText =
        `
// 클라이언트에서 Token 저장과 활용 경험만 있습니다.

// 로그인 절차 생략

// 1. 로그인 성공 시 서버에서 보내주는 Token을 쿠키에 저장하는 함수
export const saveToken = async (token) => {
    const { access_token } = token
    document.cookie = await \`token=\${access_token}; path=/; Secure; SameSite=Strict\`;
    // 토큰 저장 및 간단한 설정

    const saveCheck = await loadToken()
    console.log(saveCheck === access_token)
    // 토큰이 제대로 저장됬는지 토큰을 불러오는 함수를 실행 후 확인
    return saveCheck === access_token ? true : false
}

// 2. 쿠키에 저장된 토큰을 불러오는 함수
export const loadToken = () => {
    const token = 'token=';
    const decodedCookie = decodeURIComponent(document.cookie)
    const cookieDivide = decodedCookie.split('; ');

    for (let i = 0; i < cookieDivide.length; i++) {
        let cookie = cookieDivide[i];
        if (cookie.indexOf(token) === 0) {
            return cookie.substring(token.length, cookie.length);
        }
    }
    return null; 
}

// 3. HTTP 요청시 토큰 사용
server.interceptors.request.use(
  config => {
      const token = loadToken()
      if(token) {
          config.headers.Authorization = \`Bearer \${token}\`
      }
      return config
  }
)
// 미들웨어 설정

export const profileGet = async () => {
    const result = await server.post('주소')
    return result.data
}
// HTTP 요청 (요청 결과에 대한 작업은 Mutation을 통해 처리)

// server (axois.create을 통한 baseUrl을 담은 변수) 를 통한 HTTP 요청 시
// 자동으로 Token 전송.
`


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}




export default Function5;

