
import { CodeBlock, dracula } from 'react-code-blocks';
import { useNavigate } from "react-router-dom";

function Function6() {
    const navigate = useNavigate();
    return (
        <div className="SampleFucPage">
            <div className="exf-section1">
                <div className="exf-info">
                    <p className="suse">Function6 - TagWord / BaseBall_Team 내 function</p>
                </div>
                <div className="exf-title">
                    <h5 className="suse">TransAction을 통한 작업 단위 변경</h5>
                </div>
                {/* <div className="exf-return">
                    <p className="suse"
                        onClick={() => navigate('/project/baseballteam')}
                    > Project(BaseBall_Team)로 돌아가기</p>
                </div> */}
            </div>
            <div className="exf-section2">
                <div className="exf-overview">
                    <p className="suse">
                        2개 이상의 DB데이터 변경 시 트랙잭션을 사용하여 작업단위를 하나로 통일하여 잘못된 데이터 입력을 방지 하도록 하였습니다.<br />
                        Core API 방식과 Callback API 방식을 둘 다 사용해봤습니다.
                    </p>
                </div>
                <div className="exf-used">
                </div>
            </div>

            <div className="exf-section3">
                <div className="exf-step">
                    <div className="exf-step-title">
                        <h6 className="suse">TransAction(Callback API)</h6>
                    </div>
                    <div className="exf-step-preinfo">
                        <p className="suse">
                            이용자가 야구 팀에 신청을 넣을 경우 *transaction의 사용을 봐주시면 감사하겠습니다. :)
                        </p>
                    </div>
                    <div className="exf-step-code">
                        <Code1 />
                    </div>
                </div>
                <div className="exf-step">
                    <div className="exf-step-title">
                        <h6 className="suse">TransAction(Core API)</h6>
                    </div>
                    <div className="exf-step-preinfo">
                        <p className="suse">
                            이용자가 소설 책을 생성할 경우 *transaction의 사용을 봐주시면 감사하겠습니다. :)
                        </p>
                    </div>
                    <div className="exf-step-code">
                        <Code2 />
                    </div>
                </div>

            </div>
            <div className="exf-section4">
                <div className="exf-review">
                    <h6 className="suse">
                        Callback API
                    </h6>
                    <p className="suse">
                        콜백 방식은 session.withTransaction을 통해 하나의 함수를 생성하고, 그 안에서 DB 변경 작업을 진행합니다. 각 변경의 성공 여부는 콜백 데이터로 확인하며, 실패할 경우 강제로 err를 생성해야 하는 점에서 불편함을 느꼈습니다. (checkPoint - Callback API를 잘못 사용하고 있었던 것 같습니다. Callback API는 하나의 함수 안에서 작업을 실행하는 것이 맞지만, 그 감싸고 있는 함수를 통해 에러 상황을 쉽게 확인할 수 있습니다.)
                    </p>
                </div>
                <div className="exf-review">
                    <h6 className="suse">
                        Core API
                    </h6>
                    <p className="suse">
                        startTransaction과 endTransaction으로 시작과 끝을 설정한 후, 그 안에서 이루어지는 DB 데이터 변경 작업에 트랜잭션 세션을 부여합니다. 이상이 없을 경우 commitTransaction을 통해 작업을 수행하고, 문제가 발생할 경우 자동으로 abortTransaction이 실행되어 작업이 중단되는 만큼 Callback API 보다 간편함을 느꼈습니다.
                    </p>
                </div>
                <div className="exf-review">
                    <h6 className="suse">
                        Callback VS Core
                    </h6>
                    <p className="suse">
                        MongoDB에 제시한 문서(6.0v)를 보면 Core API를 사용하라고 권장하고 있는 만큼 Core API가 코드가 직관적이며 간편하다는 느낌을 많이 받았습니다.
                    </p>
                </div>
                <div className="exf-review">
                    <h6 className="suse">
                        아직은 미숙한 TransAction 사용
                    </h6>
                    <p className="suse">
                        TransAction 은 다양한 에러 상황시 Retry 기능을 포함한 다양한 에러 처리 방법이 있으며 데이터 읽기 쓰기에 따른 권한과 다양한 옵션등 모르는 부분이 너무나 많기에 공부가 많이 필요하다고 느꼈습니다.
                    </p>
                </div>
            </div>
        </div>
    )
}



function Code1() {

    const codeText =
        `
// BaseBall_Team Server
// post.js 

router.get('/jointeam/:id', check_login, myrating_onteam, async (req, res) => {
    // check_login - 로그인 확인용 미들웨어
    // myrating_onteam - 현재 이용자가 해당 팀 내 등급 확인 용 미들웨어
    try {
        const user = await db.collection('user').findOne({ _id: new ObjectId(req.user._id) })
        // 현재 이용자 데이터를 변수 할당
        if (req.myrating) return res.status(200).send({ success: false, false_code: 5, message: '이미 가입하셨습니다. -> false_code : 5' })
        // 예외처리 (이미 가입한 상태)
        if (!Object.values(user.team).includes(null)) return res.status(200).send({ success: false, false_code: 5, message: '더 이상 가입 하 실수 없습니다. -> false_code : 5' })
        // 예외처리 (2개의 팀이 이미 소속 된 경우)

        const req_params = await req.params.id
        // params 변수 할당 (팀 new ObjectId)
        const team_data = await db.collection('team').findOne({ url: req_params })
        // params 변수 값으로 팀 데이터 가져오기

        let user_team = await user.team
        // 현재 이용자의 팀 데이터 변수 할당 
        if (user && user_team) {
            for (const key in user_team) {
                if (user_team[key] === null) {
                    user_team[key] = team_data._id;
                    break;
                }
            }
        }
        // 이용자가 가입할 수 있는 팀 빈 자리가 있는지 확인 후
        // 해당 팀 id 할당

        var notificationTextUser_summary =  \`[팀] 가입 신청이 완료되었습니다.\`
        var notificationTextUser =  \`[ 팀 \${team_data.name}] 가입 신청이 완료되었습니다.\`
        var notificationTextTeam_summary =  \`[팀] 가입 신청이 들어왔습니다.\`
        var notificationTextTeam =  \`[ 팀 \${team_data.name}] 가입 신청이 들어왔습니다.\`
        // 이용자와 팀 리더에게 보낼 데이터 생성

        const work_for_db = await join_transaction(
        user, // user = DB에 저장된 이용자 데이터
        user_team, // user_team = DB에 저장된 이용자 데이터 중 팀 관련 항목에서 현재 팀 정보를 삽입한 데이터
        team_data, // team_data = 이용자가 가입하려고 하는 team 데이터
        [notificationTextUser_summary, notificationTextUser],
        [notificationTextTeam_summary, notificationTextTeam],
        // 배열들 = 유저에게 보낼 알림내용 (짧은버전, 긴버전) / 팀리더에게 보낼 알림내용 (짧은버전, 긴버전)
        session // session = 트랜잭션을 위한 session
        )
        // 이후 코드는 생략
    } catch (err) {
        log('Router [/jointeam/:id] catch')
        res.status(500).send({ errMessage: err })
    }
})


// 트랜잭션 수행할 함수이며 총 5가지의 DB 변경을 수행함

async function join_transaction(user, user_team, team_data, notificationUser, notificationTeam, session) {
    try {
        const transaction_result = await session.withTransaction(async () => {
            // 세션으로 트랜잭션 단위 설정
            const work1 = await db.collection('user').updateOne(
            // 유저 데이터 내 팀 관련 데이터 변경
                { _id: new ObjectId(user._id) },
                {$set: {team: user_team}}, { session })
            if (await work1.modifiedCount !== 1) {
            // modifuedCount를 통해 수정 결과 체크.
                throw new Error('work1')
            }
            const work2 = await db.collection('member')
            // 맴버 콜랙션 내 데이터 생성
                .insertOne({
                    team_id: new ObjectId(team_data._id),
                    user_id: new ObjectId(user._id),
                    nickname: user.nickname,
                    join_date: "",
                    member_rating: 4,
                    confirm: false,
                    application_date: moment().format('YYYY-MM-DD HH:mm')
                }, { session })
            if (await !work2.acknowledged) {
            // acknowledged 로 Insert 결과 확인
                throw new Error('work2')
            }
            const work3 = await db.collection('team').updateOne(
            // 팀 데이터 내 맴버 숫자 추가
                { _id: new ObjectId(team_data._id) },
                {$inc: {member_count: 1}}, { session })
            if (await work3.modifiedCount !== 1) {
                throw new Error('work3')
            }
            const work4 = await db.collection('notification_post')
            // 알림 콜랙션에 데이터 생성 (팀 리더용)
                .insertOne({
                    recevie_id: new ObjectId(team_data._id),
                    send_id: new ObjectId(user._id),
                    recevie_userid: new ObjectId(team_data.team_leader),
                    major_category: 1,
                    sub_category: 1,
                    note: notificationTeam,
                    Notification_date: moment().format('YYYY-MM-DD HH:mm'),
                    confirm: false,
                    type: 'post'
                }, { session })
            if (await !work4.acknowledged) {
                throw new Error('work4')
            }
            const work5 = await db.collection('notification_user')
            // 알림 콜랙션에 데이터 생성 (이용자 용)
                .insertOne({
                    recevie_id: new ObjectId(user._id),
                    send_id: new ObjectId(team_data._id),
                    major_category: 1,
                    sub_category: 1,
                    note: notificationUser,
                    Notification_date: moment().format('YYYY-MM-DD HH:mm'),
                    confirm: false,
                    type: 'user'
                }, { session })
            if (await !work5.acknowledged) {
                throw new Error('work5')
            }
        })
        return transaction_result;
    } catch (err) {
        console.log('function [team_transaction] catch')
        throw err
    }
}


// 총 5개의 DB데이터 변경 (수정 2개 / 생성 3개)를 하나의 작업단위로 묶어 처리
`


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}

function Code2() {

    const codeText =
        `
// TagWord Server
// routes 폴더 내 index.ts

 app.post('/newbook', async (req, res) => {
    try {
      const adminColl = await adminDB.findOne({ _id: new ObjectId(DB_ADMIN) })
      // DB에서 종합 데이터를 가져오기
      const bookId = await new ObjectId();
      // ObjectId 값 생성
      const { password1, password2, ...inputData } = await req.body;
      // 클라이언트에서 보낸 데이터 변수 할당
      if (!adminColl) return res.status(404).send({ success: false, message: 'admin data 못 찾는중 ' })
      // 예외처리 ( 종합데이터 찾지 못함 )
      if (password1 !== password2) return res.status(404).send({ success: false, message: '비밀번호 다르다.' })
      // 예외처리 ( 이용자가 입력한 2개의 비밀번호가 서로 상이)
      const hashedPassword = await bcrypt.hash(password1, Number(HASH))
      // 비밀번호 HASH 처리

      const newBookNum = await adminColl.books
      // 소설책 연번을 가져옴

      const transactionResult = await newBookTransAction(
        newBookNum, // 새로 생성되는 소설 책 번호
        bookId, // 새로 생성되는 소설 데이터의 ObjectId
        inputData, // 이용자가 입력한 데이터 (소설주제 등)
        hashedPassword // 이용자가 입력한 비밀번호
        )
      // 이후 코드 생략
    } catch (err) {
      console.log(err)
      return res.status(500).send({ message: 'Server Error' })
    }
  })

    // transaction 수행
    const newBookTransAction = async (books: number, bookId: ObjectId, inputData: inputData, password: string) => {
    const session = client.startSession();
    // 세션 변수 할당
    try {
      session.startTransaction();
      // 트랙잭션 시작
      await adminDB.updateOne({ _id: new ObjectId(DB_ADMIN) }, { $set: { [books + 1]: bookId }, $inc: { books: 1 } }, { session })
      // 종합데이터 수정
      await db.collection(\`Counter\`).insertOne(
      // 카운터 콜랙션에 현재 소설 데이터 insert 작업
        {
          _id: bookId,
          ...inputData,
          password: password,
          books: books + 1,
          sentenceCount: 0,
          paragraphCount: 1,
          pageClose: false,
          lastWords: ["", "", ""],
          newStart: true
        }, { session })
      await db.createCollection(\`Sentence_\${books + 1}\`, { session })
      // 현재 소설 연번으로 된 콜렉션 생성
      await session.commitTransaction();
      // session에 저장된 DB 변경들 작업 수행
      console.log('트랜잭션 완료')
    } catch (err) {
      await session.abortTransaction();
      // session에 저장된 DB 변경들 실행 정지
      console.log(\`트랜잭션 에러 \${err}\`)
    } finally {
      session.endSession();
      // session 종료
    }
  }

`


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}




export default Function6;

