import { useParams } from 'react-router-dom'
import './../../css/function/functionEX.css'


import Function1 from './Function1';
import Function2 from './Function2';
import Function3 from './Function3';
import Function4 from './Function4';
import Function5 from './Function5';
import Function6 from './Function6';
import Function7 from './Function7';
import Function8 from './Function8';
import Function9 from './Function9';
import Function10 from './Function10';
import Function11 from './Function11';

//  EX => explanation (설명)
function FunctionEx() {
    let { id } = useParams();
    console.log(id)
    const projectNames = {
        1: <Function1 />, // Joi
        2: <Function2 />, // 클라이언트 선 조치 / 후 서버데이터
        3: <Function3 />, // 감정표현 수 문장
        4: <Function4 />, // Function 만 있는 Component
        5: <Function5 />, // Passport
        6: <Function6 />, // Transaction
        7: <Function7 />, // 미들
        8: <Function8 />, // Kakao
        9: <Function9 />, // 즉각처리
        10: <Function10 />, // react calender
        11: <Function11 />, // react window
    }

    const checkParams = () => {
        const projectComponent = projectNames[id]
        return projectComponent
    }

    return (
        <div className='FunctionEX'>
            {checkParams()}
        </div>
    )
}

//  projectEx MW - 1024


export default FunctionEx
