import { Routes, Route, Navigate, useLocation } from 'react-router-dom'
import { useEffect } from 'react';
import './css/App.css';

import NavPage from './components/common/NavPage';
import MainPage from './components/main/MainPage';

import AboutPage from './components/about/AboutPage';

import ProjectEX from './components/project/ProjectEX';
import FunctionEx from './components/function/FunctionEX';

import FunctionPage from './components/function/FunctionPage';
import ArchitecturePage from './components/architecture/ArchitecturePage';

function App() {
  return (
    <div className="App">
      <div className='Cotainer'>
        <ScrollToTop />
        <NavPage />
        <Routes>
          <Route path='/' element={<MainPage />} />
          <Route path='/about' element={<AboutPage />} />
          <Route path='/project' element={<Navigate to="/" />} />
          <Route path='/project/:id' element={<ProjectEX />} />
          <Route path='/function' element={<FunctionPage />} />
          <Route path='/function/:id' element={<FunctionEx />} />
          <Route path='/architecture' element={<ArchitecturePage />} />
        </Routes>

      </div>
      <div className='Footer'>
        <p className='suse'>포트폴리오</p>
        <p className='suse'>도유현</p>
      </div>
    </div>
  );
}


function ScrollToTop() {
  const { pathname } = useLocation();
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [pathname])
  return null;
}


export default App;


