function Code1() {

    const codeText =
        `

// React-Window 구현 코드 (이하 RW)
// ★ 페이지 하단 링크를 통하여 직접 사용해보실 수 있습니다 ★
// 실제 코드와 달리 설명에 따라 코드가 나열되어 있습니다. 


수 없이 많은 데이터를 효율적으로 사용할 수 있는 RW를 사용하여
무한 스크롤 가로 달력을 구현했습니다.



1. 날짜를 가져오는 함수 입니다.
const today = moment(new Date());
const loadDateData = async (criteria, length, direction) => {
    // 날짜를 불러오는 함수
    // criteria => 날짜 기준
    // length => 기준 된 날짜에서 얼마 만큼 날짜를 불러올 껀지
    // direction => 이전의 날짜인지? 이후의 날짜인지?  방향

    const returnData = [] // 리턴 할 데이터
    const startPoint = direction === 2 ? 0 : -length; // 반복문 조건 1
    const endPoint = direction === 1 ? 0 : length; // 반복문 조건 2

    for (var i = startPoint; i <= endPoint; i++) {
        returnData.push(moment(criteria).add(i, 'days'))
    } // 반복문으로 날짜 데이터 retrunData 에 push
    return returnData
}

2. useEffect를 통해 페이지 Mount시 위 함수를 실행하여 최초의 날짜 데이터를 가져옵니다. 
   오늘 기준으로 앞뒤로 20일치, 즉 오늘을 포함하여 총 41일치의 데이터

    useEffect(() => {
        const mountLoad = async () => {
            const mountData = await loadDateData(today, 20, 0)
            setDateData(mountData);
            setDataLoading(true);
        }
        mountLoad()
    }, [])


3. 달력은 일주일 단위, 즉 7일씩 화면에 노출, 앞뒤 버튼까지
   가로달력의 최상휘 Div를 AutoSizer 라이브러리를 통하여 9등분을 한 후 
   그 안에서 버튼 + 달력  + 버튼  형식으로 구성

<AutoSizer> 
// 자동으로 높이와 너비를 트래킹해주는 라이브러리
    {({ height, width }) => {
        const division9 = width / 9 
        // 9개의 구역으로 나눔
        if (!dataLoading) return; 
        return (
            <div
                className='list-wrap'
                style={{
                    width: width,
                    height: height
                }}
            >
            // 최상위 Div
                <div
                    style={{
                        width: division9, 
                        height: height,
                    }}
                    className='list-but'
                    onClick={() => listBut(-7)}
                >
                    <p>⬅️</p>
                </div>
                // preBut
                <List
                // List = RW 라이브러리 최상위 Component
                    className='no-scroll'
                    height={height} 
                    // 높이
                    itemCount={dateData?.length} 
                    // 데이터의 수
                    itemSize={division9} 
                    // 데이터를 보여줄 Div의 너비
                    width={division9 * 7} 
                    // RW 의 너비 (일주일 단위씩 랜더링을 하기 위하여 x7 작업)
                    layout='horizontal'
                    // 레이아웃 가로 
                    onItemsRendered={handleTile}
                    // 데이터를 가져올 함수
                    // 4번 항목에서 설명
                    ref={listRef}
                    initialScrollOffset={(20 - 3) * division9}
                    // 시작 시 스크롤 위치 
                    // 처음 Mount 시 총 41일치
                    // 오늘날짜는 21번에 있기에 화면 중앙에 배치하기 위하여
                    // 20 -3 작업을 함
                >
                    {({ index, style }) => (
                        // 날짜 하나하나의 Component
                        <Tile
                            index={index}
                            style={style}
                            data={dateData}
                            onClick={clickTile}
                            chooseIndex={chooseDate}
                            selectDate={selectDate}
                        />
                    )}

                </List>
                <div
                    style={{
                        width: division9,
                        height: height
                    }}
                    className='list-but'
                    onClick={() => listBut(7)}
                >
                    <p>➡️</p>
                </div>
                // nextBut
            </div>
        )
    }
    }
</AutoSizer>


여기까지 정리하자면 
페이지 시작시 오늘 기준으로 앞뒤 20일치 데이터를 가져옵니다.
달력에 일주일 단위씩 보이기 위하여 AutoSizer 라이브러리를 사용하여 
달력 최상위 div 의 너비를 트래킹 하여 /9 작업
버튼 2개 + 날짜 9개 하여 배치 하였습니다.



4. 날짜 추가 랜더링 작업 
onItemsRendered={handleTile} 내 handleTile 함수입니다.

const handleTile = ({
    visibleStartIndex, 
    visibleStopIndex,
    // 현재 스크롤 안에 있는 데이터 중 
    // 가장 앞 index 와 가장 뒤 index
}) => {
    setCenter(Math.round((visibleStartIndex + visibleStopIndex) / 2))
    // 7일씩 노출 되는 데이터인 만큼 가장 앞 인덱스와 가장 뒤 인덱스를 더한 후 2로 나눠 
    // 반올림하여 중간에 위치한 날짜 인덱스를 추출 후 저장

    if (visibleStartIndex < 10) {
        // 현재 보고있는 날짜 중 가장 앞 인덱스가 10보다 작으면
        if (!addLoading) return; // 로딩 체크
        const addDate = async () => {
            setAddLoading(false) // 로딩 실행
            const newData = await loadDateData(dateData[0], 50, 1)
            // 날짜 데이터 중 가장 앞 데이터 기준 더 앞선 50일치 데이터를 가져옴
            setDateData(pre => [...newData, ...pre])
            // 데이터 파일에 넣음
            if (listRef.current) {
                listRef.current.scrollTo(listRef.current.state.scrollOffset + newData.length * 44.44444444);
            }
            // 데이터가 추가 된 만큼 스크롤이 밀리니깐, 원래 스크롤에서 안밀리게 작업
            setAddLoading(true);
            // 로딩 해제
        }
        addDate();
    }
    if ((dateData.length - 10) < visibleStopIndex) {
        // 현재 보고 있는 날짜 중 가장 뒤 인덱스가 데이터 끝 인덱스 기준으로 차이가 10 이하일 경우
        const lastLength = dateData.length - 1 // 기준 설정
        if (!addLoading) return; // 로딩 체크
        const addDate = async () => {
            setAddLoading(false) // 로딩 실행
            const newData = await loadDateData(dateData[lastLength], 50, 2)
            // 날짜 데이터 중 가장 뒤 데이터 기준 더 뒤 50일치 데이터를 가져옴
            setDateData(pre => [...pre, ...newData])
            // 데이터 파일에 넣음
            setAddLoading(true);
            // 로딩 해체
        }
        addDate();
        // RW에 적용
    }
}


참고 - Tile Component

const Tile = ({ index, style, data, onClick, chooseIndex, selectDate }) => {
    const indexDate = moment(data[index]);
    const d = indexDate.format('dd')
    const dd = indexDate.format('DD')
    const todayCheck = today.isSame(indexDate, 'day')
    const chooseCheck = chooseIndex === index
    const selectCheck = selectDate.origindate.includes(indexDate.format('YYYY-MM-DD'))
    return (
        <div
            style={{
                ...style,
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'flex-start',
                alignItems: 'center',
                userSelect: 'none',
                cursor: 'pointer'
            }}
            onClick={() => onClick(index)}
        >
            <p style={{
                color: chooseCheck ? 'blue' : 'black',
                fontWeight: chooseCheck ? 'bold' : 'normal'
            }}>
                {todayCheck ? '오늘' : d} <br />
                {dd}
            </p>
            <div
                style={{
                    display: selectCheck ? 'block' : 'none',
                    width: '5px',
                    height: '5px',
                    marginTop: '5px',
                    borderRadius: '5px',
                    background: 'blue'
                }}
            />
        </div>
    )
};

`


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}




export default Function11;
