function Code1() {

    const codeText =
        `
// BaseBall_Team Server
// middleware.js 내 check_login

async function check_login(req, res, next) { 
    if (req.user) { 
    // Passport.js의 기능을 사용하여 req.user로 현재 로그인 여부 체크.
        next() 
    // 이상이 없다면 API 실행
        return
    } else {
        res.status(200).send({ success: false, false_code: 1, message: "로그인 안됨" })
    // 예외 처리 
        return
    }
}

// 적용 된 API 예 
// mypage.js 내 GET /mypage_data


router.get('/mypage_data', check_login, async (req, res) => {
    // 마이페이지 Mount 시 이루어지는 GET 요청
    try {
        const user = await db.collection('user').findOne({ _id: req.user._id })
        const myteam = await Object.values(user.team).filter(val => val)
        const myteam_info = await mypage_team_conversion(myteam, user._id)
        const mynotification_info = await mypage_notification_conversion(user._id)
        
        return res.status(200).send({
            success: true,
            my_info: user,
            myteam_info: myteam_info,
            mynotification_user: mynotification_info[0],
            mynotification_post: mynotification_info[1]
        })
    } catch (err) {
        console.log('Router [mypage/] catch')
        console.log(err)
    }
})
`


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}

function Code2() {

    const codeText =
        `
// BaseBall_Team Server
// middleware.js 내 myrating_onteam

async function myrating_onteam(req, res, next) {
    try {
        const req_params = await req.params.id
        const team_data = await db.collection('team').findOne({ url: req_params })
        if (!team_data) return res.status(200).send({ success: false, false_code: 4, message: 'url 해당되는 자료 없음 -> false_code : 4' })
        const user = await db.collection('user').findOne({ _id: new ObjectId(req.user?._id) })
        if (!user) return next(); // 비로그인일때
        const user_team_check = await Object.values(user.team).some(val => val?.equals(team_data?._id))
        if (!user_team_check) return next(); // 유저 콜렉션 확인 비가입자
        const member_check = await db.collection('member').findOne({
            team_id: new ObjectId(team_data._id),
            user_id: new ObjectId(user._id),
            member_rating: { $exists: true }
        })
        if (!member_check) return log('이 경우에는 user,member DB간 서로 데이터가 불일치 하는 경우 확인해야함')
        req.myrating = await member_check.member_rating
        return next();
    } catch (err) {
        console.log('middleware [myrating_onteam] catch')
        console.log(err)
    }
}

// 팀 관련 페이지 접속 시, 팀 관련 기능 실행 시 작동되는 미들웨어입니다.
// 미들웨어 작동원리
1. 클라이언트에서 보낸 데이터(params)를 통한 팀 데이터 검색 후 확인 (없다면 예외처리)
2. 현재 이용자가 로그인 중인지 아닌지 확인 후 비로그인일때는 바로 next() 로 API 실행
3. 로그인 된 이용자가 팀에 가입했는지 확인 후 비가입자라면 바로 next() 로 API 실행
4. 서로 다른 DB 내용이 같은지 교차확인*
5. 현재 이용자의 팀 내 등급을 req(Object) 에 새로운 Key - value 로 넣어서 next() 처리

- 만약 관리자 페이지 접속 및 관리자 권한 기능을 사용할 경우:
미들웨어를 통해 전달된 데이터를 바탕으로 API 단계에서 예외 처리를 구현하고 있습니다. 
등급에 따라 권한이 다르기 때문에 권한별로 미들웨어를 따로 만드는 대신, 
권한을 찾아 데이터에 포함시키는 미들웨어를 사용하여 한 번에 처리할 수 있도록 했습니다.
`
    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}




export default Function7;

