function Code1() {

    const codeText =
        `
// Post.tsx 내 Bulletinpost Component
// Page Mount 시 useEffect 를 통하여 게시판 관련 데이터를 GET
// 데이터를 각각의 State에 맞게 설정

// 해당되는 Element Code만 가져왔습니다.

<div className='post-box'> 
    <div className={userLike ? 'like-button userlike' : 'like-button'}
    // 좋아요 여부에 따른 className 배정으로 style 변화.
        onClick={async () => {
            if (!userId) return alert('게시물 좋아요를 하려면 로그인을 해야합니다.')
            setPostdataLike((pre) => userLike ? pre - 1 : pre + 1) // ★ 선 조치 (게시물 좋아요 수 줄이기)
            bulletin_like() // HTTP 요청이 있는 함수 실행
        }} >
        // 좋아요 버튼 클릭시
        <p>{userLike ? "♥" : "♡"}</p>
        // 좋아요에 따른 Text 변화
    </div>
</div>


async function bulletin_like() {
    setUserLike(!userLike) 
    // ★ 선 조치 (해당 데이터에 대한 현재 이용자에 대한 좋아요 여부)
    await fetch('/api/post/bulletin/like/' + id, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        credentials: 'include',
        body: JSON.stringify({
            userLike: !userLike,
        })
    }).catch((err) => {
        console.error(\`오류코드 \${ err.status } = \` + err);
    })
}
    `


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}


function Code2() {

    const codeText =
        `
// Main.tsx 내  FcSocketIoFisrtGet Component 접속 시
// Params 데이터를 Socket.io를 통해 서버로 전송
// 서버에선 Params에 맞는 소설 책이 있는지 검증 후
// 없다면 예외처리 / 있다면 그 소설에 맞게 Room Join(Socket.io 기능) 처리 후
// 데이터를 클라이언트로 전송
// 소설에 대한 데이터는 모두 redux Toolkit Store에 저장

// 감정표현은 총 2곳에서 할 수 있지만 한 곳만 코드 작성

// ClickSentence.tsx 내 ClickSentencePage Component


    const mutation = useMutation(
        (data: { sentenceData: SentenceStoreSliceType, idx: number, params: string }) => expressionPost(data), {
        onSuccess: (data) => { },
        // HTTP 요청이 이상없이 완료 되면 아무 작업도 진행 하지 않음.
        onError: (error, data) => {
            dispatch(removeExpression([data.idx, data.sentenceData.no]))
            dispatch(removeExpressionT([data.idx, data.sentenceData.no]))
            alert('감정표현 클릭이 실패 하였습니다. 잠시 후 다시 시도해주시길 바랍니다.')
        }
        // ★에러시 - 에러 발생 시, 선 작업을 진행했던 내용을 롤백 후 안내
    })
    const expressionClickFc: ExpressionClickFc = (sentenceData, idx) => {
        if (!id) return; // 예외처리.
        mutation.mutate({ sentenceData: sentenceData, idx: idx, params: id }) 
        // react-Query 내 Mutation 을 활용하여 HTTP 요청 실행
        dispatch(addExpression([idx, sentenceData.no]))
        dispatch(addExpressionT([idx, sentenceData.no]))
        // ★선작업 - 그 와 동시에 Redux Store에 저장된 데이터 변경
    }


     // JSX 
        <div className='ClickSentencePageWrap flex column jc-start ai-start'>
            <div className='clickSentenceContent flex row jc-start ai-center'>
                 //생략
            </div>
            <div className='clickSentenceExpression flex row ai-center'>
                {['🤣', '🥹', '👍', '❤️'].map((val, idx) => <div key={idx}
                    onClick={() => expressionClickFc(clickSentenceStoreSlice, idx)}
                >
                    {val}   {clickSentenceStoreSlice.expression[idx]}
                </div>
                // 유저가 해당 버튼 클릭시 expressionClickFc 함수 실행
                // 내가 클릭한 문장이 가지고 있는 감정표현의 수와
                // 내가 클릭 한 감정의 IDX를 매개인자로 실행
                )}
            </div>
            <div className='clickSentenceInfo flex row ai-center'>
                //생략
            </div>
        </div>

    `


    return (
        <CodeBlock
            text={codeText}
            language='javascript'
            showLineNumbers={false}
            theme={dracula}
        />
    )
}



export default Function2;
